.hero {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(1200px 800px at 50% 10%, rgba(0, 184, 217, 0.09), transparent 60%),
              radial-gradient(900px 600px at 60% 70%, rgba(124, 92, 255, 0.08), transparent 60%),
              #0B1220;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  box-sizing: border-box;
  z-index: 2;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(0, 184, 217, 0.06) 0%, rgba(124, 92, 255, 0.04) 70%, transparent 100%);
  pointer-events: none;
  z-index: -1;
}

.hero-content {
  max-width: 1200px;
  width: 100%;
  text-align: center;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.25rem;
  padding: 0 1rem;
  box-sizing: border-box;
}

/* New bold title */
.mega-title {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 800;
  font-size: clamp(2.4rem, 8vw, 7rem);
  color: #E6EEF3;
  margin: 0.5rem 0 0.25rem 0;
  line-height: 0.95;
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem 0.7rem;
  justify-content: center;
}

.word {
  position: relative;
  display: inline-block;
  padding: 0.1rem 0.4rem;
  border-radius: 10px;
}

.word-purple { background: linear-gradient(90deg, rgba(124,92,255,0.25), rgba(124,92,255,0)); }
.word-teal   { background: linear-gradient(90deg, rgba(0,184,217,0.25), rgba(0,184,217,0)); }
.word-orange { background: linear-gradient(90deg, rgba(255,159,67,0.25), rgba(255,159,67,0)); }
.word-pink   { background: linear-gradient(90deg, rgba(255,99,170,0.25), rgba(255,99,170,0)); }
.word-lime   { background: linear-gradient(90deg, rgba(137,207,0,0.25), rgba(137,207,0,0)); }

/* Tagline */
.hero-tagline {
  font-size: 1.3rem;
  color: #9AA7B2;
  margin: 0;
  max-width: 700px;
}

/* Avatar */
.hero-profile { position: relative; }
.avatar-wrapper { position: relative; width: 140px; height: 140px; }
.avatar-image {
  width: 140px;
  height: 140px;
  border-radius: 9999px;
  object-fit: cover;
  object-position: center top;
  border: 2px solid rgba(255,255,255,0.08);
  position: relative;
  z-index: 2;
}
.avatar-glow {
  position: absolute; inset: -6px; border-radius: 9999px;
  background: conic-gradient(from 180deg, #7C5CFF, #00B8D9, #FF63AA, #7C5CFF);
  filter: blur(10px); opacity: 0.3; z-index: 1;
}
.greeting-pill {
  position: absolute; top: -28px; left: -28px; z-index: 3;
  background: rgba(255,99,170,0.15); color: #FF63AA;
  border: 1px solid rgba(255,99,170,0.35);
  padding: 6px 10px; border-radius: 9999px;
  font-size: 12px; font-weight: 600;
}

/* Floating labels */
.floating-labels { position: absolute; inset: 0; pointer-events: none; }
.floating-pill {
  position: absolute;
  padding: 10px 14px; border-radius: 9999px; font-size: 12px; font-weight: 600;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  border: 1px solid rgba(255,255,255,0.15);
  box-shadow: 0 8px 24px rgba(0,0,0,0.25);
  pointer-events: auto;
}
.pill-pink  { background: rgba(255,99,170,0.15); color: #FF63AA; }
.pill-blue  { background: rgba(124,92,255,0.15); color: #7C5CFF; }
.pill-green { background: rgba(0,184,217,0.15); color: #00B8D9; }
.pill-orange{ background: rgba(255,159,67,0.15); color: #FF9F43; }

/* CTA and overview keep existing styles */
.cta-button {
  background: #00B8D9;
  border: 1px solid #00B8D9;
  color: #FFFFFF;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Space Grotesk', sans-serif;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 0.5rem;
}

.cta-button:hover {
  background: #00A2C0;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 184, 217, 0.3);
}

/* Remove old quick overview space if present */
.quick-overview { display: none; }

.overview-card {
  background: #0D1626;
  border: 1px solid rgba(255, 255, 255, 0.03);
  border-radius: 15px;
  padding: 2rem;
  backdrop-filter: blur(10px);
  transform: translateY(0);
  transition: transform 0.3s ease;
  box-shadow: 0 8px 24px rgba(3, 6, 12, 0.6);
}

.overview-card:hover { transform: translateY(-5px); }

.overview-card h3 {
  color: #E6EEF3;
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1.3rem;
  margin: 0 0 1rem 0;
  text-align: center;
}

.overview-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.overview-section h4 {
  color: #00B8D9;
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
  font-family: 'Space Grotesk', sans-serif;
}

.overview-section p {
  color: #9AA7B2;
  font-size: 0.9rem;
  margin: 0;
  line-height: 1.4;
}

.overview-links {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.overview-links a {
  color: #9AA7B2;
  font-size: 1.5rem;
  transition: all 0.3s ease;
  position: relative;
}

.overview-links a:hover { color: #00B8D9; transform: translateY(-2px); }

.overview-links a::after {
  content: attr(title);
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.7rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  white-space: nowrap;
}

.overview-links a:hover::after { opacity: 1; }

@media (max-width: 768px) {
  .hero { padding: 24px 15px; }
  .hero-content { padding: 0 0.5rem; gap: 1rem; }
  .mega-title { font-size: clamp(2.2rem, 10vw, 4rem); }
  .hero-tagline { font-size: 1.05rem; max-width: 320px; }
  .avatar-wrapper { width: 110px; height: 110px; }
  .avatar-image { 
    width: 110px; 
    height: 110px; 
    object-position: center top;
  }
  .floating-pill { 
    font-size: 11px; 
    padding: 8px 12px; 
    /* Better mobile positioning */
    position: absolute !important;
  }
  
  /* Mobile-specific positioning for floating pills */
  .floating-labels {
    /* Ensure proper containment on mobile */
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  
  /* Adjust pill positions for mobile screens */
  .floating-pill:nth-child(1) { /* Hello, I'm George */
    left: 5% !important;
    top: 15% !important;
  }
  .floating-pill:nth-child(2) { /* CS Student */
    left: 75% !important;
    top: 20% !important;
  }
  .floating-pill:nth-child(3) { /* Athens, Greece */
    left: 5% !important;
    top: 70% !important;
  }
  .floating-pill:nth-child(4) { /* @justsubway */
    left: 75% !important;
    top: 80% !important;
  }
  
  /* Mobile CTA button styling */
  .cta-button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    margin-top: 7rem;
    transform: translateY(60px);
  }
} 